import ReactDOM from 'react-dom'
import React, { createRef, useCallback, useEffect, useMemo, useRef, useState } from 'react'
import { Link as GatsbyLink } from 'gatsby'
import styled from 'styled-components'
import range from 'lodash/range'
import mapValues from 'lodash/mapValues'
import Measure from 'react-measure'

import SheetLoader from './SheetLoader'
import Container from 'components/Container'
import useTableHeader from 'hooks/useTableHeader'
import useYearControl from 'hooks/useYearControl'
import useQuarterControl from 'hooks/useQuarterControl'
import Text from 'components/Text'
import Box from 'components/Box'
import Link from 'components/Link'
import PageSimulator from './PageSimulator'
import DramTimeline from './DramTimeline'
import parseDate from 'utils/parseDate'
import Pagination from 'components/Pagination'
import Flex from 'components/Flex'
import NoteBox from './NoteBox'
import PageTitle from './PageTitle'

const colors = {
  dram: ['#72afc7', '#234b68'],
  flash: ['#00af9c', '#0b6660'],
}

const typeColor = {
  NOR: colors.flash[0],
  NAND: colors.flash[1],
}

const StyledTable = styled.table`
background: linear-gradient(0deg, rgba(227,227,227,0.85), rgba(238,238,238,0.3));
height: 100%;
th, td {
  border: 3px solid #fff;
  line-height: 2.5;
}
`

const borderRightWidth = 12
const barWidth = 50

const FabRow = ({
  fab,
  data,
  startYear,
  yearSpan,
  color,
  timelineRef,
  scale,
  boundBox,
  startQuarter,
  count,
}) => {
  const barHeight = 300 / count
  const [timelinePoses, setTimelinePoses] = useState([])
  const lineRefs = useMemo(() => data.map(() => [
    createRef(),
    createRef(),
  ]), [data])

  useEffect(() => {
    setTimelinePoses(lineRefs.map(([start, end]) => {
      const startPos = start.current?.getBoundingClientRect()
      const endPos = end.current?.getBoundingClientRect()
      if (!endPos) return {}
      const height = endPos.bottom - startPos.top
      return mapValues({
        left: startPos.left - boundBox.left,
        width: endPos.right - startPos.left,
        top: startPos.top + height / 2 / 2 - boundBox.top,
        height: height / 2,
      }, v => v / scale)
    }))
  }, [lineRefs, boundBox, yearSpan])

  return (
    <>
      <tr>
        <Box
          as="td"
          whiteSpace="pre-wrap"
          bg="#cacaca"
          textAlign="center"
          style={{ borderRightWidth }}
          color="#c63b38"
          fontSize="27.5px"
          fontWeight="bold"
        >{fab}</Box>
        {range(yearSpan * (yearSpan > 3 ? 2 : 4)).map((_, q, { length }) => {
          const isLast = q === length - 1
          return (
            <td
              key={q}
            >
              {data.map((row, i) => {
                let ref
                if (q === 0) ref = lineRefs[i][0]
                if (isLast) ref = lineRefs[i][1]
                return (
                  <Box
                    key={i}
                    height={barHeight * 2}
                    mt={i === 0 && (barHeight / 2)}
                    mb={i === data.length - 1 && (barHeight / 2)}
                    ref={ref}
                  />
                )
              })}
            </td>
          )
        })}
      </tr>
      {timelineRef?.current && ReactDOM.createPortal((
        <>
          {data.map((d, i) => (
            <Box key={i} position="absolute" style={timelinePoses[i]}>
              <DramTimeline
                data={d.timeline}
                colors={[d.color || color]}
                startQuarter={startQuarter}
                startYear={startYear}
                yearSpan={yearSpan}
                thickness={barWidth}
              >
                {d.name}
              </DramTimeline>
              {/* <Box
                textAlign="right"
                fontSize="27.5px"
                color="white"
                position="absolute"
                top="50%"
                right="1.5em"
                transform="translateY(-50%)"
                fontWeight="bold"
                whiteSpace="nowrap"
              >
                {d.name}
              </Box> */}
            </Box>
          ))}
        </>
      ), timelineRef.current)}
    </>
  )
}

const TechTable = ({ data, headerRow, startYear, startQuarter, name, scale, yearSpan = 3 }) => {
  const [boundBox, setBoundBox] = useState({})
  const years = useMemo(() => {
    const isPerfect = startQuarter === 1 || yearSpan > 3
    const makeQuarter = q => `${yearSpan > 3 ? 'H' : 'Q'}${q + 1}`
    return range(yearSpan + (isPerfect ? 0 : 1)).map(y => {
      if (isPerfect) return range(yearSpan > 3 ? 2 : 4).map(makeQuarter)
      if (y === 0) return range(startQuarter - 1, 4).map(makeQuarter)
      if (y === yearSpan) return range(startQuarter - 1).map(makeQuarter)
      return range(4).map(makeQuarter)
    })
  }, [yearSpan, startQuarter])
  const timelineParser = useCallback((d) => {
    const tech = headerRow.find(h => /Technology/i.test(h.Header))?.accessor(d) ?? ''
    const density = headerRow.find(h => /density/i.test(h.Header))?.accessor(d) ?? ''
    const start = headerRow.find(h => /Start/i.test(h.Header))?.accessor(d) ?? ''
    const end = headerRow.find(h => /End/i.test(h.Header))?.accessor(d) ?? ''
    const type = name === 'flash' ? headerRow.find(h => /Type/i.test(h.Header))?.accessor(d) ?? '' : ''
    return {
      name: `${type} ${tech} ${density}`,
      timeline: [[parseDate(start), parseDate(end, 1)]],
      color: typeColor[type],
    }
  }, [headerRow, name])
  const groupByFab = useMemo(() => {
    const fabAccesor = headerRow.find(h => /fab/i.test(h.Header))?.accessor
    if (!fabAccesor) return []
    return data.reduce((all, row) => {
      const fab = fabAccesor(row)
      let l = all.length
      if (fab) {
        l = all.push({ fab, data: [] })
      }
      all[l - 1]?.data.push(timelineParser(row))
      return all
    }, [])
  }, [data, headerRow, timelineParser])
  const timelineRefs = useMemo(() => groupByFab.map(() => createRef()), [groupByFab])
  return (
    <Box>
      <PageTitle>
        {data[0]?.[0]}
      </PageTitle>
      <Measure
        bounds
        onResize={contentRect => {
          setBoundBox(contentRect.bounds)
        }}
      >
        {({ measureRef }) => (
          <Box
            position="absolute"
            left="170px"
            top="162px"
            right="183px"
            bottom="130px"
            ref={measureRef}
          >
            <StyledTable>
              <thead>
                <tr>
                  <Box as="th" fontWeight="900" bg="#efefef" rowSpan={2} width="222px" style={{ borderRightWidth }} />
                  {years.map((span, y) => (
                    <Box
                      key={y}
                      color="white"
                      as="th"
                      bg="#c63b38"
                      colSpan={span.length}
                      fontSize="26px"
                      fontWeight="700"
                    >{startYear + y}</Box>
                  ))}
                </tr>
                <tr>
                  {years.reduce((all, quarters, y) => all.concat(quarters.map(q => (
                    <Box
                      as="th"
                      bg="#cacaca"
                      key={q + y}
                      width="112px"
                      fontSize="24px"
                    >{q}</Box>
                  ))), [])}
                </tr>
              </thead>
              <tbody>
                {groupByFab.map(({ fab, data: rowData }, f) => (
                  <FabRow
                    key={fab}
                    data={rowData}
                    fab={fab}
                    startYear={startYear}
                    yearSpan={yearSpan}
                    startQuarter={yearSpan > 3 ? 1 : startQuarter}
                    timelineRef={timelineRefs[f]}
                    color={colors[name][f]}
                    scale={scale}
                    boundBox={boundBox}
                    count={data.length}
                  />
                ))}
              </tbody>
            </StyledTable>
            <Flex.HStack mt="1em" justifyContent="flex-end" alignItems="center" spacing="1.25em">
              <NoteBox bg="#9fa0a0">NOTE</NoteBox>
              <Box>Roapmaps are subject to change.</Box>
            </Flex.HStack>
            {groupByFab.map(({ fab }, f) => <Box.FullAbsolute key={fab} ref={timelineRefs[f]} />)}
          </Box>
        )}
      </Measure>
    </Box>
  )
}

const RadioInput = ({ name, value, onChange, children, checked }) => (
  <Box as="label">
    <input type="radio" name={name} value={value} onChange={onChange} checked={checked} />
    <Box as="span" ml="0.5em">{children}</Box>
  </Box>
)

const yearSpanValues = [
  { label: '3 Years', value: 3 },
  { label: '7 Years', value: 7 },
]

const SheetData = ({ data, name }) => {
  const { startYear, yearControl } = useYearControl()
  const { startQuarter, quarterControl } = useQuarterControl()
  // const [selectedRow, setSelectedRow] = useState({})
  const [page, setPage] = useState(0)
  const [yearSpan, setYearSpan] = useState(3)

  const headerRow = useTableHeader(data, 0)
  const tableData = useMemo(() => data ? data.slice(1) : [], [data])
  const pages = useMemo(() => {
    return tableData.reduce((all, row) => {
      let i = all.length - 1
      if (row[0]) {
        i = all.push([]) - 1
      }
      all[i]?.push(row)
      return all
    }, [])
  }, [tableData])
  const notFab = useMemo(() => headerRow.every((h => !/fab/i.test(h.Header))), [headerRow])

  return (
    <>
      <Flex.HStack alignItems="center">
        {yearControl}
        {quarterControl}
        <Flex.HStack spacing="1.5em">
          {yearSpanValues.map(({ value, label }) => (
            <RadioInput
              name="yearSpan"
              value={value}
              onChange={() => setYearSpan(value)}
              checked={yearSpan === value}
              key={value}
            >
              {label}
            </RadioInput>
          ))}

        </Flex.HStack>
      </Flex.HStack>
      {pages && pages[page] && (
        notFab ? (
          <Text fontWeight="bold" my="1em">Invalid Sheet Format.</Text>
        ) : (
          <>
            <Pagination
              pageCount={pages.length}
              pageRangeDisplayed={5}
              marginPagesDisplayed={2}
              onPageChange={({ selected }) => setPage(selected)}
            />
            <PageSimulator noBg name={`${name}-tech-roadmap`} key={yearSpan}>
              <TechTable
                data={pages[page]}
                headerRow={headerRow}
                startYear={startYear}
                startQuarter={startQuarter}
                name={name}
                yearSpan={yearSpan}
              />
            </PageSimulator>
          </>
        )
      )}
    </>
  )
}

const TechRoadmap = ({ name }) => {
  return (
    <Container>
      <Link as={GatsbyLink} to={`/${name}`}>
        {`< Home`}
      </Link>
      <SheetLoader name={name}>
        <SheetData />
      </SheetLoader>
    </Container>
  )
}

export default TechRoadmap
