import React, { useMemo, useState } from 'react'
import { getQuarter } from 'date-fns'

import Flex from 'components/Flex'
import Text from 'components/Text'
import Box from 'components/Box'

const useQuarterControl = () => {
  const [startQuarter, setStartQuarter] = useState(() => getQuarter(new Date()))
  const quarterControl = useMemo(() => (
    <Flex as="label" my="1em" alignItems="center">
      <Text mr="0.5em">起始季度</Text>
      <Box width="6em">
        <Box
          as="select"
          p="0.5em"
          onChange={e => setStartQuarter(+e.target.value)}
          value={startQuarter}
        >
          <option value="1">Q1</option>
          <option value="2">Q2</option>
          <option value="3">Q3</option>
          <option value="4">Q4</option>
        </Box>
        {/* <Box
          as="input"
          maxWidth="100%"
          type="number"
          lineHeight="2"
          value={startQuarter}
          max={4}
          min={1}
          onChange={e => setStartQuarter(+e.target.value)}
        /> */}
      </Box>
    </Flex>
  ), [startQuarter])
  return {
    startQuarter,
    quarterControl,
  }
}

export default useQuarterControl
